/**
 * Component that returns text indicating a server error
 *
 * @component
 * @returns {JSX} The React component.
 */
const Error500 = () => {
  return (
    <>
      <p className="big-number">500</p>
      <p className="text">Dsol, une erreur rseau s'est produite.</p>
    </>
  );
};

export default Error500;
